  .highlight,
  .highlight .w {
    color: lighten(#93a1a1,10%);
    background-color: black;
  }
  .highlight .err {
    color: lighten(#002b36,10%);
    background-color: lighten(#dc322f,10%);
  }
  .highlight .c,
  .highlight .ch,
  .highlight .cd,
  .highlight .cm,
  .highlight .cpf,
  .highlight .c1,
  .highlight .cs {
    color: lighten(#657b83,10%);
  }
  .highlight .o,
  .highlight .ow,
  .highlight .p,
