  .highlight .gh {
    color: lighten(#268bd2,10%);
    background-color: lighten(#002b36,10%);
    font-weight: bold;
  }
  .highlight .k,
  .highlight .kn,
  .highlight .kp,
  .highlight .kr,
  .highlight .kv {
