.copy-code-button-div {
  position: relative;
}

.copy-code-button {
  display: none;
  
  float: right;
  width: 2rem;
  height: 2rem;
  padding: 0;
  cursor: pointer;

  transition-duration: 0.1s;
  transition-property: background-color;

  border: 0;
  background-color: white;

  position: absolute;
  right: 0;
  top: 0;

  &:hover,
  &:active,
  &:focus {
    background-color: darken(white, 20%);
  }

  &:active,
  &:focus {
    outline: none;
  }

  &.copied:after {
    background: darken(white, 10%);
    color: lighten(black, 10%);
    content: "Copied!";
    font-size: 1rem;
    font-weight: 400;
    padding: 0.2rem;
    border-radius: 0.5rem;

    display: block;
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translate(-.4rem, -50%)
  }
}

.copy-code-button>*{
  left: 50%;
  top: 50%;
  width: 1rem;
  position: absolute;
  transform: translate(-50%, -50%);
}
